  .header-nav-item.has-dropdown .btn.button-group-switch-item-link.header-nav-item-link:before {
    font-size: 1.7rem;
    font-weight: 400;
    line-height: 1.7rem;
  }
}

.disabled {
  cursor: not-allowed !important;

  opacity: .5;
}

input {
  &[type=text].disabled,
  &[type=tel].disabled {
    background-color: #dfdfdf;
  }
  &[type=submit].disabled {
    background-color: #dfdfdf;
    &:hover {
      background-color: #dfdfdf;
    }
  }
}

select .disabled,
textarea.disabled {
  background-color: #dfdfdf;
}

a.body-text.disabled,
input.body-text.disabled.link {
  text-decoration: none;
}

a.body-text.disabled:hover,
input.body-text.disabled.link:hover {
  color: #333;
}

a.non-clickable,
input.non-clickable.link {
  cursor: default;
  pointer-events: none;
}

div#rh-arrow {
  &.rh-above,
  &.rh-below {
    top: auto !important;
    right: auto !important;
    bottom: 20px !important;
    left: 9% !important;

    margin: 0 !important;
  }
}

.hide-rh {
  #rh-snippet,
  .product-details-online-chat .btn {
    display: none !important;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  div#rh-arrow {
    display: none !important;
  }
}

.lazy-content .lazy-load-content-holder {
  min-height: 12.8rem !important;

  background: url(/assets/img/ajax-loader.gif) 50% 50% no-repeat !important;
  &.lazy-content-loaded {
    min-height: 1px !important;

    background: 0 0 !important;
  }
}
